import posts from '../assets/posts.json';

export const postService = { query, addPostComment, updateLikeCounter }

function query() {
    return posts;
}
function addPostComment(postId, comment) {
    const post = posts.find(post => post.id === postId);
    if (post) {
        post.comments.push(comment);
    }
}
function updateLikeCounter(postId, isAdd) {
    const post = posts.find(post => post.id === postId);
    if (post) {
        if (isAdd) {
            post.likesCounter++;
        } else post.likesCounter--;
    }
}
