<template>
  <section v-if="post" class="post-preview">
    <header class="post-header">
      <img
        class="author-profile"
        src="static/img/default-avatar.png"
        alt="profile"
      />
      <h2 class="author-name">{{ postAuthor.name }}</h2>
    </header>
    <div class="product-img-container">
      <img class="product-img" :src="product.imgUrl" alt="product" />
    </div>
    <div class="product-info">
    <p class="product-name">{{ product.name }}</p>
    <p class="product-price">{{ product.price }}</p>
    <div class="product-description">{{ product.title }}</div>
    </div>

    <div class="btns-container">
      <button class="like-btn" @click="addLike">
        <i :class="likeBtnClass"></i>
        <span class="likes-counter">{{ likeCount }}</span>
      </button>
      <button class="comment-btn" @click="isCommentOpen = !isCommentOpen">
        <i class="far fa-comment"></i>
        <span class="comments-counter">{{ commentsCount }}</span>
      </button>
    </div>

    <comment-list
      v-if="isCommentOpen"
      :comments="post.comments"
      :postId="post.id"
    />
  </section>
</template>

<script>
import { userService } from "../services/user.service";
import { productService } from "../services/product.service";
import CommentList from "./CommentList.vue";
import { postService } from "../services/post.service";
export default {
  data() {
    return {
      postAuthor: "",
      product: "",
      isCommentOpen: false,
      isLiked: false,
    };
  },
  methods: {
    onCommentClicked() {
      this.isCommentOpen = !this.isCommentOpen;
    },
    addLike() {
      if (this.isLiked) {
        postService.updateLikeCounter(this.post.id, false);
      } else {
        postService.updateLikeCounter(this.post.id, true);
      }
      this.isLiked = !this.isLiked;
    },
  },
  computed: {
    commentsCount() {
      const count = this.post.comments.length;
      if (count === 0) return "";
      return count > 9 ? "9+" : count;
    },
    likeCount() {
      const count = this.post.likesCounter;
      if (count === 0) return "";
      return count > 9 ? "9+" : count;
    },
    likeBtnClass() {
      let classes =
        this.post.likesCounter > 0 ? "fas fa-heart" : "far fa-heart";
      if (this.isLiked) {
        classes += " liked";
      }
      return classes;
    },
  },
  created() {
    this.postAuthor = userService.getById(this.post.authorId);
    this.product = productService.getById(this.post.productId);
  },
  props: {
    post: Object,
  },
  components: {
    CommentList,
  },
};
</script>
<style lang="scss" scoped>
header {
  display: flex;
  align-items: center;
  padding-left: 10px;
}

.post-preview {
  position: relative;
  background-color: white;
  margin-bottom: 10px;
  border-radius: 4px;
  width: 550px;
  padding: 10px 0;
  @media screen and (max-width: 768px) {
    width: 100%;
  }
}
.author-profile {
  height: 35px;
  width: 35px;
  display: inline-block;
  margin-right: 15px;
}
.product-img-container {
  max-height: 320px;
  width: 100%;
}
.product-img {
  max-height: 320px;
  width: 100%;
  object-fit: cover;
}
.product-info {
    padding: 10px;
}

button {
  background-color: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;
  transform: scale(1.5);
}
.btns-container {
    display: flex;
    justify-content: flex-end;
    padding-right: 10px;
    margin-bottom: 10px;
}
.like-btn {
  margin-right: 6px;
}

.liked {
  color: red;
}
</style>