<template>
  <section title="comment-list">
    <div class="comment-preview" v-for="comment in comments" :key="comment.commentId">
      <p>{{ comment.text }}</p>
    </div>
    <input type="text" v-model="comment.text" @keydown.enter="addComment" />
    <button @click="addComment" >Send</button>
  </section>
</template>

<script>
import { postService } from "../services/post.service";
import { utilService } from "../services/util.service";
export default {
  data() {
    return {
      comment: {
        commentId: utilService.makeId(10),
        authorId: "",
        text: "",
      },
    };
  },
  methods: {
    addComment() {
      postService.addPostComment(this.postId, this.comment);
      this.resetComment();
    },
    resetComment() {
      this.comment = {
        commentId: utilService.makeId(10),
        authorId: "",
        text: "",
      };
    },
  },
  props: {
    comments: Array,
    postId: String,
  },
};
</script>

<style>
input {
    border: 0;
    border-bottom: 1px solid;
    width: 85%;
    outline: 0;
}
.comment-preview {
    background-color: #f7f7f7;
    border-radius: 6px;
    margin-left: 15px;
    max-width: 80%;
    padding: 2px 5px;
    margin-bottom: 5px;
}
</style>